import React from "react";
import { NavLink } from "react-router-dom";
const Navbar = () =>{

  return(
    <>
        <div className="container-fluid nav__fluid">
          <div className="row">
            <div className="col-md-10 p-0 mx-auto">
<nav className="navbar navbar-expand-lg navbar-light">
  <div className="container-fluid">
    <NavLink className="navbar-brand logo" to="/"><span>M</span>Billah</NavLink>
    <button className="navbar-toggler" type="button" data-bs-toggle="collapse" data-bs-target="#navbarSupportedContent" aria-controls="navbarSupportedContent" aria-expanded="false" aria-label="Toggle navigation">
      <span className="navbar-toggler-icon"></span>
    </button>
    <div className="collapse navbar-collapse list__text" id="navbarSupportedContent">
      <ul className="navbar-nav ">
        <li className="nav-item">
          <NavLink exact activeClassName="NavLinkActive" className="nav-link active" to="/">Home</NavLink>
        </li>
        <li className="nav-item">
          <NavLink exact activeClassName="NavLinkActive" className="nav-link active" to="/protfulio">Protfulio</NavLink>
        </li>
        <li className="nav-item">
          <NavLink exact activeClassName="NavLinkActive" className="nav-link active" to="/about">About</NavLink>
        </li>
        <li className="nav-item">
          <NavLink exact activeClassName="NavLinkActive" className="nav-link active" to="/skill">Skill</NavLink>
        </li>
        <li className="nav-item">
          <NavLink exact activeClassName="NavLinkActive" className="nav-link active" to="/contact">Contact</NavLink>
        </li> 
      </ul>
    </div>
  </div>
</nav>
            </div>
          </div>   
        </div>

    </>
  )
}
export default Navbar;